import React, { useState } from 'react';
import { View, Text, TextInput, Button, StyleSheet } from 'react-native';
import Toggle from 'react-toggle';
import 'react-toggle/style.css'; // Import the styles for the toggle
import { ToastContainer, toast } from 'react-toastify';
import 'react-toastify/dist/ReactToastify.css';

const App = () => {
  const [username, setUsername] = useState('');
  const [useCache, setUseCache] = useState(false);

  const handleButtonClick = async (value) => {
    if (!username) {
      showToast('Username cannot be empty.', 'error');
      return;
    }

    await addPoints(value);
    await updateLeaderboard();
  };

  const addPoints = async (value) => {
    const headers = {
      'Content-Type': 'application/json',
      'x-api-key': 'bHqxurf8V0KU2ebPRblpYBXZ3i39w+cJKg36fWPiKfMBGIi/',
    };

    var body = JSON.stringify({});
    var url = 'https://cors-proxy-kylenguyen7-d4dea47f740b.herokuapp.com/https://apis.roblox.com/datastores/v1/universes/5042054588/standard-datastores/datastore/entries/entry/increment?=&datastoreName=playerCoins&entryKey=' + username + '&incrementBy= ' + value;

    if (value === 'Reset') {
      url = 'https://cors-proxy-kylenguyen7-d4dea47f740b.herokuapp.com/https://apis.roblox.com/datastores/v1/universes/5042054588/standard-datastores/datastore/entries/entry?=&datastoreName=playerCoins&entryKey=' + username;
      body = '0';
    }

    try {
      const response = await fetch(url, {
        method: 'POST',
        headers: headers,
        body: body,
      });

      // Check if the request was successful
      if (response.ok) {
        if (value === 'Reset') {
          showToast(`DataStores: Reset ${username}'s points to 0.`, 'success');
        } else {
          showToast(`DataStores: Gave ${username} ${value} points. New value: ${await response.text()}`, 'success');
        }
      } else {
        // Handle error scenarios
        showToast('Error sending request. Please try again.', 'error');
      }
    } catch (error) {
      console.error('Error sending request:', error);
      showToast('An error occurred. Please try again later.', 'error');
    }
  };

  const updateLeaderboard = async () => {
    const headers = {
      'Content-Type': 'application/json',
      'x-api-key': 'bHqxurf8V0KU2ebPRblpYBXZ3i39w+cJKg36fWPiKfMBGIi/',
    };

    const body = JSON.stringify({
      message: useCache,
    });

    try {
      const response = await fetch('https://cors-proxy-kylenguyen7-d4dea47f740b.herokuapp.com/https://apis.roblox.com/messaging-service/v1/universes/5042054588/topics/UpdateLeaderboard', {
        method: 'POST', // Adjust the HTTP method as needed
        headers: headers,
        body: body,
      });

      // Check if the request was successful
      if (response.ok) {
        showToast('Messaging: Sent message to servers to update leaderboard. Cache: ' + (useCache ? 'ON' : 'OFF'), 'success');
      } else {
        // Handle error scenarios
        showToast('Error sending request. Please try again.', 'error');
      }
    } catch (error) {
      console.error('Error sending request:', error);
      showToast('An error occurred. Please try again later.' + error, 'error');
    }
  };

  const showToast = (message, type) => {
    toast[type](message, {
      position: 'bottom-right',
      autoClose: 2000, // 2 seconds
    });
  };

  return (
    <View style={styles.container}>
      <Text style={styles.title}>Points Dashboard</Text>
      <View style={styles.content}>
        <TextInput
          style={styles.input}
          placeholder="Enter username"
          value={username}
          onChangeText={(text) => setUsername(text)}
        />
        <View style={styles.buttonContainer}>
          <View style={styles.button}>
            <Button title="+100" onPress={() => handleButtonClick('100')} />
          </View>
          <View style={styles.button}>
            <Button title="+10" onPress={() => handleButtonClick('10')} />
          </View>
          <View style={styles.button}>
            <Button title="+1" onPress={() => handleButtonClick('1')} />
          </View>
          <View style={styles.button}>
            <Button title="Reset" onPress={() => handleButtonClick('Reset')} />
          </View>
          </View>
        <View style={styles.toggleContainer}>
          <Text style={styles.toggleLabel}>Caching</Text>
          <Toggle
            className="custom-toggle" // Apply custom styles via CSS
            checked={useCache}
            onChange={() => setUseCache(!useCache)}
          />
        </View>
      </View>
      <ToastContainer />
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    height: '100vh',
  },
  title: {
    fontWeight: 'bold',
    fontSize: 24,
    marginBottom: 20,
  },
  content: {
    alignItems: 'center',
  },
  input: {
    width: '80%',
    height: 40,
    borderWidth: 1,
    marginBottom: 20,
    paddingHorizontal: 10,
  },
  buttonContainer: {
    flexDirection: 'row',
    justifyContent: 'center',
    width: '100%',
  },
  button: {
    marginHorizontal: 10,
  },
  toggleContainer: {
    flexDirection: 'row',
    justifyContent: 'flex-end',
    alignItems: 'center',
    width: '100%',
    marginVertical: 10,
    paddingHorizontal: 10,
  },
  toggleLabel: {
    marginRight: 5,
  },
});

export default App;